@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');

.Timer{
    font-family: "Roboto", sans-serif;
    margin-bottom: 5vh;
    justify-self: center;
    padding: 0;
    text-align: center;
    font-size: 6em;
    color: white;
    -webkit-text-stroke: rgba(87, 87, 87, 0.842) 2px;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    pointer-events: none;
    -webkit-user-select: none;
}

.GameStyleContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5vh;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    pointer-events: none;
    -webkit-user-select: none;
}
.GridOverlay{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    padding:0;
    margin-right: auto;
    margin-left: auto;
}
.GridOverlay li{
    position: absolute;
    list-style-type: none;
}
.GameContainer{
    pointer-events: auto;
    display:flex;
    
    flex-wrap: wrap;
    padding:0;
    border: 6px solid #959595;
    width:calc(468px / var(--size-divisor));
    height:calc(468px / var(--size-divisor));
    box-shadow: 0px 0px 6px 3px rgb(228, 228, 228);
    background-color: rgb(128, 169, 168);
    
}
.GameComp{
    display: grid;
    grid-template-columns: repeat(9,1fr);
    padding:0;
    margin:0;
    width: calc(468px / var(--size-divisor));
    height: calc(52px / var(--size-divisor));
    border-radius: 2px;
    line-height: calc(50px/ var(--size-divisor));;
    text-align: center;
    
}

.GameComp>li{
    background-color: rgb(128, 169, 168);
    border: 1px solid white;
    list-style-type: none;
    font-size: calc(26px/ var(--size-divisor));
    font-weight: bold;
    color:rgb(255, 255, 255);
    cursor: pointer;
    font-family: "Roboto", sans-serif;
}
.GameComp>li:hover{
    background-color: rgb(104, 255, 210);
    transition:0.1s ease-out;
}



.PadContainer{
    cursor: pointer;
    position: absolute;
    left:0;
    top:0;
    opacity:0;
    transition:opacity 50ms ease-out;
}
.Padstyle {
    text-align: center;
    padding: 0px;
    border:2px solid rgba(104, 104, 104, 0.815);
    background-color: rgba(81, 211, 163, 0.705);
    border-radius: 2px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    width: 130px;
    line-height: 40px;
    
}
.Padstyle li{
    list-style-type: none;
    color: rgb(255, 255, 255);
    font-weight: bold;
    font-size: 26px;
    transition:50ms ease-out;
    user-select: none;
    font-family: "Roboto", sans-serif;
    
}
.Padstyle li:hover{
    background-color: rgba(255, 255, 255,0.8);
    
}
.ThickGridH1{
    width: calc(468px/ var(--size-divisor));
    height:calc(6px/ var(--size-divisor));
    background-color: white;
    margin-top:calc(156px / var(--size-divisor));
    
}
.ThickGridH2{
    width: calc(468px/ var(--size-divisor));
    height:calc(6px/ var(--size-divisor));;
    background-color: white;
    margin-top:calc(312px / var(--size-divisor));
    
}
.ThickGridW1{
    width: calc(6px/ var(--size-divisor));
    height:calc(468px/ var(--size-divisor));
    background-color: white;
    margin-left:calc(156px / var(--size-divisor));
}
.ThickGridW2{
    width: calc(6px/ var(--size-divisor));
    height:calc(468px/ var(--size-divisor));
    background-color: white;
    margin-left:calc(312px / var(--size-divisor));
   
}
@media(min-width: 176px){
    :root{
        --size-divisor:1.4
    }
}
@media(min-width: 276px){
    :root{
        --size-divisor:1.2
    }
}
@media(min-width: 376px){
    :root{
        --size-divisor:1
    }
}

@media(min-width: 476px){
    :root{
        --size-divisor:1
    }
}
